body{
	margin: 0;
	padding: 0;
}

.content {
	width: 70%;
	margin: 0 auto;
}

.header-wrapper {
	background-color: pink;
	width: 100%;
	top: 0;
	text-align: center;
	position: fixed;
	-webkit-transition: height 0.5s ease-in;
	-o-transition: height 0.5s ease-in;
	transition: height 0.5s ease-in;
}
.header-title {
}
.header-subtitle {
}
.logo {
	position: absolute;
	top: 0;
	left: 1.5rem;
	font-size: 4rem;
	color: black;
	-webkit-transition: color 0.2s linear;
	-o-transition: color 0.2s linear;
	transition: color 0.2s linear;
}
.logo a{
	text-decoration: none;
	color: inherit;
}

.navbar {
	position: absolute;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	right: 1.75rem;
	top: 1rem;
}

.navbar-item {
	text-decoration: none;
	color: black;
	-webkit-transition: color 0.2s linear;
	-o-transition: color 0.2s linear;
	transition: color 0.2s linear;
}
.navbar-item:hover {
	color: white;
}



